<template>
    <div class="row">
        <div class="col-lg-6 offset-lg-3 col-md-10 offset-md-1 col-sm-12 offset-sm-0 regular-top-margin">
            <h5 class="global-headtitle-page--elem">Login</h5>
            <div class="global-input-fields--elem">
                <label>Your email:</label>
                <input type="text" placeholder="exemple@exemple.com" v-model="email">
            </div>       
            
            <div class="global-input-fields--elem">
                <label>Your password:</label>
                <input type="password" placeholder="" v-model="password">
            </div>
            
            <div class="global-input-fields--elem">
                <router-link to="/register" class="main-register-link--elem">Create Account!</router-link>
                <button class="global-btn-elements blue-skies-gradient btn--target main-next--btn custom--size" type="button" @click="login">Login</button>
            </div>

            <transition
                name="custom-classes-transition"
                enter-active-class="animated bounceInUp"
                leave-active-class="animated bounceOutDown"
            >
                <div class="fixed--layout" v-if="errorDetected">
                    <div class="main-alert-detected--area error--kit">
                        <i class="las la-times-circle"></i> {{ errorData.data.message }} <a class="main-hide--btn" @click="errorDetected = !errorDetected">Hide</a>
                    </div>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
    /* eslint-disable */
    import axios from 'axios';
    import { _login } from './../../utils/auth';
    import emit from './../../utils/emit';

    export default {
        name: 'login',
        data() {
            return {
                email: 'test@mevnauth.io',
                password: '',

                errorDetected: false,
                errorData: null
            }
        },
        created() {
            this.listenToError();
        },
        methods: {
            login() {
                _login(this.email, this.password);
            },
            listenToError() {
                emit.$on('errorDetected', $event => {
                    this.errorDetected = true;
                    this.errorData = $event;
                });
            }
        }
    }
</script>

