                    <span>{{ postedAt(comment.createdAt) }}</span>
                    <button class="global-btn-elements many-more-replies" @click="toggleReplies()" v-if="comment.replies.length > 0">{{ comment.replies.length }} More Replies</button>
                
                    <!-- {{comment.userId._id}} {{user._id}} -->
                    <actionsItem :deletedCommentFn="this.deletedCommentById" :commentId="comment.id" :postId="postId" :match="comment.userId._id === user._id" v-if="checkLoggedIn"/>
                </h5>
                <div v-html="getHtml(comment.body)"></div>

                <div class="hr"></div>

                
                    
                <!-- <div class="main-thumbs-action--elem">
