<script>
/* eslint-disable */
import axios from 'axios';

import emit from "./../../utils/emit";
import { getRelativeTime, getDecodedHtmlText, sendUpdateThumbs } from './../../utils/globalHandlers';

import replies from './replies'
import replyTextarea from './replyTextarea'
import actionsItem from './actionsItem'

import thumbsBtn from './thumbsBtn'
import thumbsBtnDataOnly from './thumbsBtnDataOnly'

export default {
    name: 'comment',
    props: ['comment', 'postId', 'user', 'checkLoggedIn'],
    data() {
        return {
            showCommentReplies: false,
            matchedCommentId: null
        }
    },
    created() {
        this.listenToEmit()
    },
    methods: {
        onThumbsBtn(commentId, type){
            sendUpdateThumbs(commentId, type)
        },
        postedAt(t){
            return getRelativeTime(t);
        },
        getHtml(h){
            return getDecodedHtmlText(h)
        },
        checkThumbs(arr, id){
            return arr.includes(id);
        },
        deletedCommentById(){
            this.matchedCommentId = JSON.parse(localStorage.getItem("deletedCommentId"));
        },
        toggleReplies(){
            this.showCommentReplies = !this.showCommentReplies
        },
        openReplies(){
            this.showCommentReplies = true
        },


        listenToEmit() {
            emit.$on("deletedDone", $event => {
                this.removeComment = false
                console.log('releaseDeleting!')
            });
        }
    },
    components: {replies, replyTextarea, actionsItem, thumbsBtn, thumbsBtnDataOnly}
}
</script>