<template>
    <div class="main-resize-textarea--elem">
        <textarea 
            class="main-reply-textarea--elem textarea" 
            placeholder="add reply.." 
            v-model="body"
            
            @input="resizeTextarea"
            @change="onChangeBody(body)"
            ref="textarea"
        >
        </textarea>
    </div>
</template>

<script>
/* eslint-disable */
import emit from "./../../utils/emit";

export default {
    data() {
        return {
            body: null,
        }
    },
    updated() {
        this.getResizeTextarea();
    },
    mounted() {
        this.$nextTick(function() {
            window.addEventListener('resize', this.getResizeTextarea);
            this.getResizeTextarea
        })
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.getResizeTextarea);
    },
    methods: {
        onChangeBody(value) {
            emit.$emit('getBody', value);
        },
        resizeTextarea(event) {
            // console.log(event.code);
            event.target.style.minHeight = '27.5px';

            let scrllH = event.target.scrollHeight;
            if(event.code == "Enter") scrllH = event.target.scrollHeight + 22;
            event.target.style.minHeight = (scrllH) + 'px';
        },
        // resize setting textarea function
        getResizeTextarea(event) {
            if(this.$refs.textarea != undefined){
                if(this.$refs.textarea.length > 0){
                    for(let i=0; i < this.$refs.textarea.length; i++){
                        this.$refs.textarea[i].style.minHeight = '27.5px';
                        this.$refs.textarea[i].style.minHeight = this.$refs.textarea[i].scrollHeight + 'px'; 
                    }
                }else{
                    this.$refs.textarea.style.minHeight = '27.5px';
                    this.$refs.textarea.style.minHeight = this.$refs.textarea.scrollHeight + 'px'; 
                }
            }
        }
    }
}
</script>

<style lang="scss">
    .main-resize-textarea--elem{
        margin-bottom: -30px;
    }
</style>