<template>
    <div class="fixed--layout">
        <transition
            name="custom-classes-transition"
            enter-active-class="animated bounceInUp"
            leave-active-class="animated bounceOutDown"
        >
            <div class="main-deleted-alert--elem" v-if="isAlerted">
                <i class="las la-trash"></i> Deleted with success!
            </div>
        </transition>
    </div>
</template>

<script>
/* eslint-disable */
import emit from "./../../utils/emit";

export default {
  data() {
    return {
      isAlerted: false
    }
  },
  created() {
    this.listenToAlertEmit();
  },
  methods: {
    listenToAlertEmit() {
      emit.$on("deletedAlert", $event => {
        this.isAlerted = true
        setTimeout(() => {this.isAlerted = false}, 2000)
      });
    }
  },
}
</script>

