<template>
    <div class="row">
        <div class="main-content-loading--area col-lg-8 offset-lg-2 col-md-10 offset-md-1 col-sm-12 offset-sm-0 regular-top-margin" v-if="!contentLoaded">
            <div class="main-fake-title--area">
                <div class="main-fake-text--elem __size24 __full gradient"></div>
                <div class="main-fake-text--elem __size24 __limit75 gradient"></div>
