                <div class="main-fake-picture--elem gradient"></div>
                <div class="main-fake-user-data--area">
                    <div class="main-fake-text--elem main-fake-name--elem gradient"></div>
                    <div class="main-fake-text--elem main-fake-created--elem gradient"></div>
                </div>
            </div>
            <div class="main-fake-content--area">
                <div class="main-fake-text--elem __size17 __full gradient"></div>
                <div class="main-fake-text--elem __size17 __full gradient"></div>
                <div class="main-fake-text--elem __size17 __limit150 gradient"></div>
                <div class="main-fake-space-elem"></div>
                <div class="main-fake-text--elem __size17 __full gradient"></div>
                <div class="main-fake-text--elem __size17 __full gradient"></div>
                <div class="main-fake-text--elem __size17 __limit150 gradient"></div>
                <div class="main-fake-space-elem"></div>
                <div class="main-fake-text--elem __size17 __full gradient"></div>
                <div class="main-fake-text--elem __size17 __limit75 gradient"></div>
            </div>
            <div class="main-fake-textarea--area gradient"></div>
        </div>
        <div class="main-single-topic--section col-lg-8 offset-lg-2 col-md-10 offset-md-1 col-sm-12 offset-sm-0 regular-top-margin" v-if="contentLoaded && post">
            <div class="main-topic-header--area">
                <h3 @click="fn()">{{ post.title }}</h3>
                <div class="main-topic-user--area">
                    <div class="main-topic-user-avatar--elem">
                        <img :src="`/static/avatars/${post.userId.photo}`" alt="#">
                    </div>
                    <div class="main-topic-user-info--elem">
                        {{ post.userId.name }}
                        <span>{{ postedAt(post.createdAt) }}</span>
                    </div>
                </div>
            </div>


            <div class="main-topic-body--area" v-html="getHtml(post.body)"></div>
            <br /><br />


            <commentTextarea :postId="post.id" :checkLoggedIn="isLoggedIn"/>


            <div class="main-comment--area">
                <h5>{{ commentsLength }} Comments</h5>
                <div class="main-comment-item--elem" v-for="(comment, x) in post.comments" :key="x">
                    <commentItem :comment="comment" :postId="post.id" :user="user" :checkLoggedIn="isLoggedIn"/>
                </div>
                <div class="main-no-comments-msg--area" v-if="commentsLength <= 0">
                    <p>No comment added yet .. be the 1st one .</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';

import emit from "./../../utils/emit";
import { getRelativeTime, getDecodedHtmlText } from "./../../utils/globalHandlers";

import commentItem from './../../components/commentsComponent/commentItem';
import commentTextarea from './../../components/commentsComponent/commentTextarea';

export default {
    name: 'show-single',
    props: ['user', 'isLoggedIn'],
    data() {
        return {
            id: this.$route.params.id,
            post: null,
            commentsLength: 0,
            
            contentLoaded: false
        }
    },
    created() {
        this.data();
        this.listenToEmit();
    },
    methods: {
        async data(){
            try {
                let res = await axios({
                    method: 'GET',
                    url: `/api/v1/topics/${this.id}`
                });

                this.post = res.data.data.data;
                this.commentsLength = (this.post.comments.length === 0) ? this.post.comments.length : this.post.commentsLength
                this.contentLoaded = true;
            } catch (err) {
                return this.$router.push('/error');
            }
        },

        postedAt(t){
            return getRelativeTime(t)
        },

        getHtml(h){
            return getDecodedHtmlText(h)
        },

        listenToEmit() {
            emit.$on("refreshComments", $event => {
                console.log("refresh this.data() fn!");
                this.data(); //update todo
            });

            emit.$on("deletedComment", $event => {
                console.log("deleted comment here!");
                this.commentsLength--
                this.data();
            });
        }
    },
    components: {commentItem, commentTextarea}
}
</script>


