<template>
    <div class="main-action--area">
        <button class="main-ellipsis--btn" v-click-outside="hide" @click="toggle" v-show="!opened"><i class="las la-ellipsis-v"></i></button>

        <div class="main-popap-actions-list--area" v-show="opened">
            <button class="main-report--btn"><i class="las la-flag"></i> Report</button>
            <button class="main-delete--btn" @click="deleteComment()" v-if="match === true"><i class="las la-trash-alt"></i> Delete</button>
        </div>

        <div class="main-deleted-alert-message--elem" v-if="deleted">
            comment deleted!
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import ClickOutside from 'vue-click-outside'

import emit from './../../utils/emit';

export default {
    props: {
        postId: {type: String}, 
        commentId: {type: String},
        match: {type: Boolean},
        deletedCommentFn: {type: Function}
    },
    data() {
        return {
            opened: false,
            deleted: false,
            deletedElement: false
        }
    },
    created() {
    },
    methods: {
        async deleteComment(){
            try {
                this.setDeletedLocalStorage()
                this.deletedCommentFn()

                await axios({
                    method: 'DELETE',
                    url: `/api/v1/comments/${this.commentId}/${this.postId}`
                });
                
                emit.$emit('deletedAlert');
                emit.$emit('deletedComment');
                
                // emit.$emit('refreshComments');
            } catch (err) {
                console.log('error!')
                console.log(err);
            }
        },

        setDeletedLocalStorage(){
            localStorage.setItem("deletedCommentId", JSON.stringify(this.commentId));
        },

        setDeletedLocalStorageArr(){
            let deletedCommentArr
            let deletedIdStored = localStorage.getItem("deletedComment")

            if( deletedIdStored === null){
                deletedCommentArr = []
            }else{
                deletedCommentArr = JSON.parse(deletedIdStored)
            }

            deletedCommentArr.push(this.commentId)
            deletedCommentArr = [...new Set(deletedCommentArr)]

            localStorage.setItem("deletedComment", JSON.stringify(deletedCommentArr));
            let deletedIdStoredStr = localStorage.getItem("deletedComment")

            console.log(JSON.parse(deletedIdStoredStr))
            
            // // retrieving our data and converting it back into an array
            // var retrievedData = localStorage.getItem("quentinTarantino");
            // var movies2 = JSON.parse(retrievedData);
            
            // //making sure it still is an array
            // // alert(movies2.length);

            // // console.log(idsGroupArr)
            // console.log(retrievedData)
            // console.log(movies2)
        },

        toggle () {
            this.opened = true
        },
    
        hide () {
            this.opened = false
        }
    },
    mounted () {
        // prevent click outside event with popupItem.
        this.popupItem = this.$el
    },
    directives: {
        ClickOutside
    }
}
</script>

