<template>
    <div class="row">
        <div class="col-lg-6 offset-lg-3 col-md-10 offset-md-1 col-sm-12 offset-sm-0 regular-top-margin">
            <h5 class="global-headtitle-page--elem">Create Account</h5>
            <div class="global-input-fields--elem">
                <label>Your fullname:</label>
                <input type="text" placeholder="Jhon Doe" v-model="name">

                <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.name"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.name.message }}</span>
            </div> 

            <div class="global-input-fields--elem">
                <label>Your email:</label>
                <input type="text" placeholder="exemple@exemple.com" v-model="email">

                <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.email"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.email.message }}</span>
            </div>       
            
            <div class="global-input-fields--elem">
                <label>Your password:</label>
                <input type="password" placeholder="" v-model="password">

                <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.password"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.password.message }}</span>
            </div>

            <div class="global-input-fields--elem">
                <label>Confirm your password:</label>
                <input type="password" placeholder="" v-model="passwordConfirm">

                <span class="main-field-erroe-detected--area"  v-if="errorDetected && errorData.data.error.errors.passwordConfirm"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.passwordConfirm.message }}</span>
            </div>
            <div class="global-input-fields--elem">
                <router-link to="/login" class="main-login-link--elem">I have an account!</router-link>
                <button class="global-btn-elements blue-skies-gradient btn--target main-next--btn custom--size" type="button" @click="register">Register</button>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import { _register } from './../../utils/auth';
import emit from './../../utils/emit';

export default {
    data() {
        return {
            name: '',
            email: '',
            password: '',
            passwordConfirm: '',

            errorDetected: false,
            errorData: null
        }
    },
    created() {
        this.listenToError();
    },
    methods: {
        register() {
            _register(this.name, this.email, this.password, this.passwordConfirm);
        },
        listenToError() {
            emit.$on('errorDetected', $event => {
                console.log($event);
                this.errorDetected = true;
                this.errorData = $event;
            });
        }
    }
}
</script>