<template>
  <div class="main-thumbs-action--elem">
    <div 
      class="main-like-btn--elem green"
      :class="{ active: userLiked, blockClick: stopClicking }"
      @click="onThumbsBtn(commentId, 'like')"
    >
      <i class="las la-thumbs-up"></i> 
      {{ likesLength }}
    </div>

    <div class="add-bar"></div>

    <div 
      class="main-dislike-btn--elem red"
      :class="{ active: userDisliked, blockClick: stopClicking }"
      @click="onThumbsBtn(commentId, 'dislike')"
    >
        <i class="las la-thumbs-down"></i> 
        {{ dislikesLength }}
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import emit from "./../../utils/emit";
import { sendUpdateThumbs } from './../../utils/globalHandlers';

export default {
  props: ['likes', 'dislikes', 'userId', 'commentId'],
  data() {
    return {
      likesLength: this.likes.length,
      dislikesLength: this.dislikes.length,

      userLiked: this.likes.includes(this.userId),
      userDisliked: this.dislikes.includes(this.userId),

      stopClicking: false
    }
  },
  created() {
    this.listenToEmit()
  },
  methods: {
    onThumbsBtn(commentId, type){
      if(!this.stopClicking){
        sendUpdateThumbs(commentId, type)
        if(type === 'like'){
          if(this.userLiked) {
            this.userLiked = false
            this.likesLength--
          }else if(this.userDisliked){
            this.userDisliked = false
            this.dislikesLength--

            this.userLiked = true
            this.likesLength++
          }else{
            this.userLiked = true
            this.likesLength++
          }
        }

        if(type === 'dislike'){
          if(this.userDisliked) {
            this.userDisliked = false
            this.dislikesLength--
          }else if(this.userLiked){
            this.userLiked = false
            this.likesLength--

            this.userDisliked = true
            this.dislikesLength++
          }else{
            this.userDisliked = true
            this.dislikesLength++
          }
        }
      }
      this.stopClicking = true

      console.log('likes :', this.likesLength, this.userLiked)
      console.log('dislikes :', this.dislikesLength, this.userDisliked)
    },

    listenToEmit() {
      emit.$on("releaseClicking", $event => {
        console.log("release click btn");
        setTimeout(() => {this.stopClicking = false}, 1000)
      });
    }
  },
}
</script>

<style lang="scss">
  .blockClick{
    cursor: not-allowed !important;
  }
</style>