import moment from 'moment';
import axios from 'axios';
import momentTimezone from 'moment-timezone';
import emit from './emit';

export const getRelativeTime = (t) => {
    let string = moment(t).fromNow()
    string = string.replace('a few seconds', '1 s');
    string = string.replace('a minute', '1m');
    string = string.replace('minutes', 'm');
    string = string.replace('an hour', '1h');
    string = string.replace('hours', 'h');
    string = string.replace('a day', '1d');
    string = string.replace('days', 'd');
    string = string.replace('a month', '1m');
    string = string.replace('months', 'm');
    string = string.replace('a year', '1y');
    string = string.replace('years', 'y');
    string = string.replace('ago', '');
    string = string.replace(' ', '');

    return string;
};

