<template>
  <div class="row">
    <div class="col-lg-10 offset-lg-1 col-md-10 offset-md-1 col-sm-12 offset-sm-0">
      <!-- {{ user }} -->
      <div class="main-setting-page--section">
        <!-- <h1>This is an about page</h1>
        <img alt="#" :src="`/static/avatars/${user.photo}`">
        <p>id: {{ user._id }} | email: {{ user.email }}</p> -->
        <div class="main-setting-menu-side--area"></div>
        <div class="main-setting-form-side--area">
          <h5 class="global-headtitle-page--elem">Your account settings <router-link to="/setting/password"><i class="las la-user-shield"></i> Security Setting</router-link></h5>

          <div class="main-ulpoad-image--elem">
            <img alt="#" :src="reviewImage" v-if="reviewImage">
            <img alt="#" :src="`/static/avatars/${user.photo}`" v-else>
            <input type='file' accept='image/*' @change="onFileChanged" ref="fileInput">
            <button class="global-btn-elements blue-skies-gradient btn--target main-next--btn" @click="$refs.fileInput.click()">Pick Photo</button>
          </div>

          <div class="global-input-fields--elem with-max-width">
            <label>Fullname:</label>
            <input type="text" placeholder="Jhon Doe" v-model="name">

            <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.name"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.name.message }}</span>
          </div>

          <div class="global-input-fields--elem with-max-width">
            <label>E-mail:</label>
            <input type="text" placeholder="exemple@exemple.com" v-model="email" :disabled="true">

            <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.email"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.email.message }}</span>
          </div>

          <button class="global-btn-elements blue-skies-gradient btn--target main-next--btn custom--size" type="button" @click="save()">Save Setting</button>
        </div>
      </div>
    </div>

    <transition
      name="custom-classes-transition"
      enter-active-class="animated bounceInUp"
      leave-active-class="animated bounceOutDown"
    >
      <div class="fixed--layout" v-if="saveDetected">
        <div class="main-alert-detected--area save--kit">
          <i class="las la-check-circle"></i> Updated With Success!
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
/* eslint-disable */
import emit from './../../utils/emit';
import axios from 'axios';
// import formData from 'formData';

export default {
  props: ['user'],
  data() {
    return {
      name: '', //this.user.name,
      email: '',//this.user.email
      image: null,
      reviewImage: null,

      saveDetected: false,

      errorDetected: false,
      errorData: null,

      contentLoaded: false
    }
  },
  created() {
    this.getUserData();
  },
  methods: {
    async save(){
      try {
        const data = this.getFormData();

        let res = await axios({
          method: 'PATCH',
          url: '/api/v1/users/updateMe',
          data
        })

        if(res){
          emit.$emit('refreshUsers')
          setTimeout(() => { this.reviewImage = null }, 250)

          this.saveDetected = true
          setTimeout(() => { this.saveDetected = !this.saveDetected }, 2500)
        }
      } catch (err) {
        console.log(err.response)

        this.errorDetected = true
        this.errorData = err.response
      }
    },
    getFormData(){
      const form = new FormData()
      form.append('name', this.name)
      if(this.image != null) form.append('photo', this.image)

      return form
    },
    onFileChanged(event){
      this.image = event.target.files[0]
      console.log(this.image)
      this.createImage(event.target.files[0])
    },

    createImage(file) {
      var reader = new FileReader()

      reader.onload = (e) => {
        this.reviewImage = e.target.result
      };
      reader.readAsDataURL(file)

      console.log(this.reviewImage)
    },


    getUserData(){
      emit.$emit('refreshUsers')
      setTimeout(() => {
        this.name = this.user.name
        this.email = this.user.email
      }, 250)
    }
  },
  computed: {
    // name(){
    //   return this.user.name
    // },
    // email(){
    //   return this.user.email
    // }
  },
}
</script>

