<template>
    <div class="row">
        <div class="main-add-topics--section col-lg-8 offset-lg-2 col-md-10 offset-md-1 col-sm-12 offset-sm-0 regular-top-margin" v-if="categories">
        <h5>Add new topic</h5>
        <div class="main-add-topics-form--area global-texteditor-style--area">
            <div class="main-add-topic-fields--elem">
                <label for="title">What's your topic?</label>
                <input type="text" name="title" id="title" placeholder="Head Title.." v-model="title">

                <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.title"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.title.message }}</span>
            </div>
            <div class="main-add-topic-fields--elem">
                <label for="body">Describe your topic!</label>
                <froala :tag="'textarea'" id="body" :config="config" v-model="body"></froala>

                <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.body"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.body.message }}</span>
            </div>
            <div class="main-add-topic-fields--elem global-select-style--area">
                    <label for="body">Which category you choose?</label>
                    <v-select v-model="categoryId" placeholder="Select category.." label="name" :options="categories"></v-select>

                <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.categoryId"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.categoryId.message }}</span>
            </div>

            <button class="global-btn-elements blue-skies-gradient btn--target" @click="createTopic">Save Now!</button>
        </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import router from './../../routes/routes'


import emit from "./../../utils/emit";
import { configFloaraWYSIWYG, getAllCategrories } from "./../../utils/globalHandlers";
export default {
    props: ['user'],
    data() {
        return {
            title: "",
            body: "",
            categoryId: "",
            // userId: this.user._id,
            config: configFloaraWYSIWYG('Description..'),

            categories: null,

            errorDetected: false,
            errorData: null
        }
    },
    created() {
        this.getCategories();
    },
    methods: {
        async createTopic(){
            try {
                let res = await axios({
                    method: "POST",
                    url: "/api/v1/topics/",
                    data: {
                        title: this.title, 
                        body: this.body, 
                        userId: this.user._id, 
                        categoryId: this.categoryId._id
                    }
                });

                if(res.data.status === "success"){
                    let id = await res.data.data.data.id;
                    router.push(`/topics/${id}`);
                }
            } catch (err) {
                console.log(err.response.data);
                this.errorDetected = true;
                this.errorData = err.response;
            }
        },


        async getCategories(){
            this.categories = await getAllCategrories()
        }
    },
}
</script>

