<template>
    <div class="main-all-topics-list--section col-lg-12 offset-lg-0 col-md-12 offset-md-0 col-sm-12 offset-sm-0 regular-top-margin">
        <h5 class="global-headtitle-page--elem">Recent Topics <router-link to="/topics/add"><i class="las la-plus"></i> Add New Topic</router-link></h5>
        
        <div class="main-content-loading--area" v-if="!contentLoaded">
            <div class="main-fake-search-bar--elem gradient"></div>

            <div class="main-fake-head-table--area globla-table--area">
                <div class="main-full-column--elem"><span class="main-fake-title--elem margin-none--kit gradient"></span></div>
                <div class="main-small-column--elem"><span class="main-fake-title--elem gradient"></span></div>
                <div class="main-small-column--elem"><span class="main-fake-title--elem gradient"></span></div>
                <div class="main-small-column--elem"><span class="main-fake-title--elem gradient"></span></div>
            </div>

            <div>
                <div class="main-fake-list-item gradient"></div>
                <div class="main-fake-list-item gradient"></div>
                <div class="main-fake-list-item gradient"></div>
                <div class="main-fake-list-item gradient"></div>
                <div class="main-fake-list-item gradient"></div>
            </div>

            <div class="main-fake-load-more gradient"></div>
        </div>

        <div class="main-topics-list--area" v-else>
            <div class="main-search-bar--area">
                <input type="text" placeholder="Find a topic.." v-model="search" @keyup="checkTopicsLength()">
            </div>

            <div class="main-head-table--area globla-table--area">
                <div class="main-full-column--elem">Topics</div>
                <div class="main-small-column--elem">Replies</div>
                <div class="main-small-column--elem">Views</div>
                <div class="main-small-column--elem">Activity</div>
            </div>
            <div v-if="getTopicsWithSplicingAndFiltring.length">
                <div class="main-topics-item--elem" v-for="(topic, index) in getTopicsWithSplicingAndFiltring" :key="index">
                    <div class="main-body-table--area globla-table--area">
                        <div class="main-full-column--elem main-custom-right-padding">
                            <router-link :to="`/topics/${topic.id}`">
                                <strong>{{ topic.title }}</strong>
                                <span>{{ topic.categoryId.name }}</span>
                            </router-link>
                            
                            <div class="main-intro-text--elem" v-html="getHtml(topic.intro)"></div>
                        </div>
                        <div class="main-small-column--elem">{{ abbreviateNumber(topic.commentsLength) }}</div>
                        <div class="main-small-column--elem">{{ abbreviateNumber(topic.views) }}</div>
                        <div class="main-small-column--elem">{{ lastActivityAt(topic.lastActivity) }}</div>
                    </div>
                </div>
            </div>
            <div class="no-content-msg--area" v-else>No Topics Founded!</div>

            <div class="global-btn-elements blue-skies-gradient main-load-more-topics-btn" v-if="defaultSplice < topicsLength" @click="loadMore">More Topics..</div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';
import router from './../../routes/routes'

import emit from "./../../utils/emit";
import { configFloaraWYSIWYG, getAllCategrories, getRelativeTime, getDecodedHtmlText, getAbbreviateNumber } from "./../../utils/globalHandlers";

export default {
    data() {
        return {
            topicsLength: 0,
            topics: [],
            defaultSplice: 5,

            search: '',

            contentLoaded: false
        }
    },
    created() {
        this.getAllTopics();
    },
    methods: {
        async getAllTopics(){
            try {
                let res = await axios({
                    method: 'GET',
                    url: "/api/v1/topics/"
                });
                
                this.topicsLength = res.data.data.data.length
                this.topics = res.data.data.data;
                this.contentLoaded = true
            } catch (err) {
                console.log(err.response.data);
            }
        },
        lastActivityAt(t){
            return getRelativeTime(t);
        },
        abbreviateNumber(v){
            return getAbbreviateNumber(v)
        },
        getHtml(h){
            return getDecodedHtmlText(h)
        },
        loadMore(){
            this.defaultSplice = this.defaultSplice + 5
        },
        checkTopicsLength(){
            if(this.search != ''){
                this.topicsLength = this.topics.filter(el => { return el.title.toLowerCase().includes(this.search.toLowerCase()) }).length
            }else{
                this.topicsLength = this.topics.length
            }
        }
    },
    computed: {
        getTopicsWithSplicingAndFiltring() {
            return this.topics.filter(el => {
                return el.title.toLowerCase().includes(this.search.toLowerCase())
            }).splice(0, this.defaultSplice)
        }
    }
    
}
</script>

