<script>
  /* eslint-disable */
  import emit from "./utils/emit";
  import { isLoggedIn, _logout, _getUser } from './utils/auth'

  import headerComponent from './components/partialsComponents/headerComponent'
  import footerComponent from './components/partialsComponents/footerComponent'
  import deletedAlert from './components/alertsComponents/deletedAlert'

  export default {
    data() {
      return {
        user: {
          photo: null//'default.jpg'
        },

        done: false
      }
    },
    created() {
      this.userInfo();
      this.listenToEmit();
    },
    methods: {
      async userInfo(){
        console.log('app.js **');
        this.user = await _getUser();
        this.done = true
      },

      isLoggedIn() {
        return isLoggedIn()
      },
      logoutUser() {
        _logout();
      },

      listenToEmit() {
        emit.$on("refreshUsers", $event => {
          console.log("refresh this.userInfo() fn!");
          this.userInfo(); //update todo
        });
      }
    },
    components:{headerComponent, footerComponent, deletedAlert}
  }
