<template>
  <div class="row">
    <div class="col-lg-10 offset-lg-1 col-md-10 offset-md-1 col-sm-12 offset-sm-0">
      <div class="main-setting-page--section" v-if="user">
        <!-- <h1>This is an about page</h1>
        <img alt="#" :src="`/static/avatars/${user.photo}`">
        <p>id: {{ user._id }} | email: {{ user.email }}</p> -->
        <div class="main-setting-menu-side--area"></div>
        <div class="main-setting-form-side--area">
          <h5 class="global-headtitle-page--elem">Password change <router-link to="/setting/profile"><i class="las la-user-edit"></i> Profile Setting</router-link></h5>

          <div class="global-input-fields--elem with-max-width">
            <label>Current Password:</label>
            <input type="password" v-model="passwordCurrent">

            <span class="main-field-erroe-detected--area" v-if="errorWrongPassword !== null"><i class="las la-times-circle"></i> {{ errorWrongPassword }}</span>
          </div>

          <div class="global-input-fields--elem with-max-width">
            <label>New Password:</label>
            <input type="password" v-model="newPassword">

            <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.password"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.password.message }}</span>
          </div>

          <div class="global-input-fields--elem with-max-width">
            <label>Confirm New Password:</label>
            <input type="password" v-model="passwordConfirm">

            <span class="main-field-erroe-detected--area" v-if="errorDetected && errorData.data.error.errors.passwordConfirm"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.passwordConfirm.message }}</span>
          </div>

          <button class="global-btn-elements blue-skies-gradient btn--target main-next--btn custom--size" type="button" @click="save()">Save Setting</button>
        </div>
      </div>
    </div>

    <transition
      name="custom-classes-transition"
      enter-active-class="animated bounceInUp"
      leave-active-class="animated bounceOutDown"
    >
      <div class="fixed--layout" v-if="saveDetected">
        <div class="main-alert-detected--area save--kit">
          <i class="las la-check-circle"></i> Updated With Success!
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios'; 

import { setAuthToken } from './../../utils/auth';
import emit from './../../utils/emit';

export default {
  props: ['user'],
  data() {
    return {
      passwordCurrent: '',
      newPassword: '',
      passwordConfirm: '',

      saveDetected: false,

      errorDetected: false,
      errorData: null,
      errorWrongPassword: null
    }
  },
  created() {
  },
  methods: {
    async save(){
      try {
        let res = await axios({
          method: 'PATCH',
          url: '/api/v1/users/updateMyPassword',
          data: {
            passwordCurrent: this.passwordCurrent,
            password: this.newPassword,
            passwordConfirm: this.passwordConfirm
          }
        });
        
        setAuthToken(res.data.token)

        this.saveDetected = true
        setTimeout(() => { this.saveDetected = !this.saveDetected }, 2500)

        this.errorDetected = false
        this.errorData = null
        this.errorWrongPassword = null
      } catch (err) {
        console.log(err.response, err.response.status)

        if(err.response.status === 500){ 
          this.errorDetected = true
          this.errorData = err.response 
        }
        (err.response.status === 401) ? this.errorWrongPassword = err.response.data.message : this.errorWrongPassword = null ; 
      }
    },
  }
}
</script>

