    display: flex;

    .main-setting-menu-side--area{
      flex-basis: 275px;
      height: 100%;
      background: linear-gradient(125deg, #1b1464, #4f4c85);
      padding: 25px;
      display: none;
    }
    .main-setting-form-side--area{
      flex-basis: 100%; //calc(100% - 275px);
      padding: 25px;
    }
  }
</style>