/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'
import { isLoggedIn } from './../utils/auth'

// import Home from './../views/Home.vue'
import settingProfilePage from './../views/dashboard/setting-profile-page.vue'
import settingPasswordPage from './../views/dashboard/setting-password-page.vue'
import e404Pgae from './../views/404-error-page.vue'

// Auth routes
import loginPage from './../views/auth/login-page.vue'
import registerPage from './../views/auth/register-page.vue'

// Topics routes
import allTopicsPgae from './../views/topics/all-topics-page.vue'
import singleTopicPage from './../views/topics/single-topic-page.vue'
import createTopicPage from './../views/topics/create-topic-page.vue'


Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/', name: 'topics-page', component: allTopicsPgae,
      meta: { allowAnonymous: true }
    },


    {
      path: '/topics/add', name: 'create-topic-page', component: createTopicPage,
    },
    {
      path: '/topics/:id', name: 'single-topic-page', component: singleTopicPage, props: true,
      meta: { allowAnonymous: true }
    },


    {
      path: '/login', name: 'login', component: loginPage,
      meta: { allowAnonymous: true }
    },
    {
      path: '/register', name: 'register', component: registerPage,
      meta: { allowAnonymous: true }
    },
    { 
      path: '/setting/profile',  name: 'setting', component: settingProfilePage,
    },
    { 
      path: '/setting/password',  name: 'setting', component: settingPasswordPage,
    },

    {
      path: '/error', name: '404-page', component: e404Pgae,
      meta: { allowAnonymous: true }
    },
    { 
      path: '*', redirect: '/error' 
    },
  ]
})

router.beforeEach((to, from, next) => {
  if ((to.name == 'login' && isLoggedIn()) || (to.name == 'register' && isLoggedIn())) { next({ path: '/' }) }
  else if (!to.meta.allowAnonymous && !isLoggedIn()) { next({ path: '/login'}) }
  else { next() }  
})

export default router