<template>
    <div>
        <div class="main-add-comment--area global-texteditor-style--area add-postion--css">
            <!-- <textarea placeholder="add comment.." v-model="body" cols="100" rows="5"></textarea><br /> -->
            <!-- <ckeditor v-model="body" :config="config"></ckeditor> -->
            <froala :tag="'textarea'" :config="config" v-model="body"></froala>
            <button class="global-btn-elements blue-skies-gradient btn--target custom--size" @click="sendComment(postId, body)">Add Comment</button>

            <span class="main-field-erroe-detected--area add-custom-top-comment--kit" v-if="(checkLoggedIn) && (errorDetected && errorData.data.error.errors.body)"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.body.message }}</span>
        </div>

        <transition
            name="custom-classes-transition"
            enter-active-class="animated bounceInUp"
            leave-active-class="animated bounceOutDown"
        >
            <div class="fixed--layout" v-if="errorDetected  && !checkLoggedIn">
                <div class="main-alert-detected--area error--kit">
                    <i class="las la-times-circle"></i> {{ errorData.data.message }} <a class="main-hide--btn" @click="errorDetected = !errorDetected">Hide</a>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';

import emit from "./../../utils/emit";
import { configFloaraWYSIWYG } from "./../../utils/globalHandlers";

import errorAlert from './../alertsComponents/errorAlert';

export default {
    name: 'commentTextare',
    props: ['postId', 'checkLoggedIn'],
    data() {
        return {
            body: null,
            config: configFloaraWYSIWYG('Add your comment..'),

            errorDetected: false,
            errorData: null
        }
    },
    methods: {
        async sendComment(postId, body){
            try {
                
                const res = await axios({
                    method: 'POST',
                    url: `/api/v1/comments/${postId}`,
                    data: {
                        body: this.body
                    }
                });

                if(res){ 
                    emit.$emit('refreshComments');
                    this.body = '';
                    this.errorDetected = false;
                }
            } catch (err) {
                console.log(err.response.data);
                this.errorData = err.response;
                this.errorDetected = true;
            }
        }
    },
    components: {errorAlert}
}
</script>