<template>
    <div>
        <div class="main-add-comment-reply--area" v-if="checkLoggedIn" ref="reply">
            <button class="global-btn-elements blue-skies-gradient main-show-reply-form--elem" @click="toggleReplyFormArea()" v-show="!toggleReplyTextarea">Reply</button>
            <div class="main-reply-form--area global-texteditor-style--area add-postion--css" v-show="toggleReplyTextarea">
                <!-- <textarea 
                    class="main-reply-textarea--elem textarea" 
                    placeholder="add reply.." 
                    v-model="body"
                    
                    @input="resizeTextarea"
                    ref="textarea"
                    >
                </textarea> -->
                <!-- <textareaElement /> -->
                <froala :tag="'textarea'" :config="config" v-model="body"></froala>
                <div class="btn--target remove-margin-right--kit">
                    <button class="global-btn-elements blue-skies-gradient" @click="sendReply(postId, commentId, body)">Add Reply</button>
                    <button class="global-btn-elements midnight-city-gradient" @click="toggleReplyTextarea = !toggleReplyTextarea">Cancel</button>
                </div>

                <span class="main-field-erroe-detected--area add-custom-top-reply--kit" v-if="errorDetected && errorData.data.error.errors.body"><i class="las la-times-circle"></i> {{ errorData.data.error.errors.body.message }}</span>
            </div>
        </div>
        <div class="main-three-dots--box" v-show="threeDots">
            <div class="main-three-dots-loader--kit"></div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';

import emit from "./../../utils/emit";
import { configFloaraWYSIWYG } from "./../../utils/globalHandlers";

import textareaElement from './textareaElement';

export default {
    name: 'replyTextare',
    props: {
        postId: {type: String}, 
        commentId: {type: String},
        checkLoggedIn: {type: Boolean},
        openRepliesFn: {type: Function},
        index: {type: Number}
    },
    data() {
        return {
            body: null,
            config: configFloaraWYSIWYG('Add reply..'),
            toggleReplyTextarea: false,

            errorDetected: false,
            errorData: null,

            threeDots: false
        }
    },
    created() {
        this.listenToEmit();
    },
    methods: {
        async sendReply(postId, commentId, body){
            try {
                this.toggleReplyTextarea = false;
                setTimeout(() => {this.threeDots = true}, 100)

                const res = await axios({
                    method: 'POST',
                    url: `/api/v1/comments/${postId}/reply/${commentId}`,
                    data: {
                        body
                    }
                });

                if(res){ 
                    emit.$emit('refreshComments')
                    this.body = ''
                    this.errorDetected = false

                    setTimeout(() => {
                        this.threeDots = false
                        this.openRepliesFn()
                    })
                }
            } catch (err) {
                console.log(err.response.data);
                this.errorDetected = true;
                this.errorData = err.response;
            }
        },
        toggleReplyFormArea(){
            emit.$emit('close-all-reply-form');
            this.toggleReplyTextarea = !this.toggleReplyTextarea;
            
            setTimeout(() => {
                window.scrollBy(0, 125);
            }, 50);
        },

        listenToEmit() {
            emit.$on("getBody", $event => {
                this.body = $event;
            });

            emit.$on("close-all-reply-form", $event => {
                this.toggleReplyTextarea = false
            });
        }
    },
    components: {textareaElement}
}
</script>