<template>
    <div class="comments-and-replies--area" v-if="matchedReplyId !== reply.id">
        <div class="main-avatar--elem"><img :src="`/static/avatars/${reply.userId.photo}`" alt="#"></div>
        <div class="main-comments--elem">
            <div class="main-comment-and-user-info--elem">
                <h5>
                    {{ reply.userId.name }} 
                    <span>{{ postedAt(reply.createdAt) }}</span> 
                    <button class="global-btn-elements many-more-replies" @click="toggleReplies()" v-if="reply.repliesLength > 0">{{ reply.repliesLength }} More Replies</button>

                    <actionsItem :deletedCommentFn="this.deletedCommentById" :commentId="reply.id" :postId="reply.postId" :match="reply.userId._id === user._id" v-if="checkLoggedIn"/>
                </h5>

                <p v-html="getHtml(reply.body)"></p>

                <div class="hr"></div>

                <!-- <div class="main-thumbs-action--elem">
                    <div class="main-like-btn--elem green active" v-if="checkLoggedIn && checkThumbs(reply.likes, user._id)" @click="onThumbsBtn(reply.id, 'like')"><i class="las la-thumbs-up"></i> {{ reply.likes.length }}</div>
                    <div class="main-like-btn--elem green" v-else @click="onThumbsBtn(reply.id, 'like')"><i class="las la-thumbs-up"></i> {{ reply.likes.length }}</div>

                    <div class="add-bar"></div>

                    <div class="main-dislike-btn--elem red active" v-if="checkLoggedIn && checkThumbs(reply.dislikes, user._id)" @click="onThumbsBtn(reply.id, 'dislike')"><i class="las la-thumbs-down"></i> {{ reply.dislikes.length }}</div>
                    <div class="main-dislike-btn--elem" v-else @click="onThumbsBtn(reply.id, 'dislike')"><i class="las la-thumbs-down"></i> {{ reply.dislikes.length }}</div>
                </div> -->
                <thumbsBtn :likes="reply.likes" :dislikes="reply.dislikes" :userId="user._id" :commentId="reply.id" v-if="checkLoggedIn"/>
                <thumbsBtnDataOnly :likes="reply.likes" :dislikes="reply.dislikes" :userId="user._id" :commentId="reply.id" v-else/>
            </div>

            <replyTextarea 
                :openRepliesFn="this.openReplies"
                :postId="reply.postId" 
                :commentId="reply.id"
                :checkLoggedIn="checkLoggedIn"
            ></replyTextarea>
            
            <replies :replies="reply.replies" v-show="showReply" :user="user" :checkLoggedIn="checkLoggedIn"/>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';

import emit from "./../../utils/emit";
import { getRelativeTime, getDecodedHtmlText, sendUpdateThumbs } from './../../utils/globalHandlers';

import replies from './replies'
import replyTextarea from './replyTextarea'
import actionsItem from './actionsItem'

import thumbsBtn from './thumbsBtn'
import thumbsBtnDataOnly from './thumbsBtnDataOnly'


export default {
    name: 'reply-item',
    props: ['reply', 'user', 'checkLoggedIn'],
    data() {
        return {
            showReply: false,
            matchedReplyId: null
        }
    },
    created() {
    },
    methods: {
        onThumbsBtn(commentId, type){
            sendUpdateThumbs(commentId, type)
        },
        postedAt(t){
            return getRelativeTime(t);
        },
        getHtml(h){
            return getDecodedHtmlText(h)
        },
        checkThumbs(arr, id){
            return arr.includes(id);
        },
        deletedCommentById(){
            this.matchedReplyId = JSON.parse(localStorage.getItem("deletedCommentId"));
        },
        toggleReplies(){
            this.showReply = !this.showReply
        },
        openReplies(){
            console.log('open this sheet.')
            this.showReply = true
        }
    },
    components: {
        replies: () => import('./replies.vue'), 
        replyTextarea,
        actionsItem,
        thumbsBtn,
        thumbsBtnDataOnly
    }
}
</script>

