import router from './../routes/routes'
import emit from './emit'
import decode from 'jwt-decode'
import axios from 'axios'

const REST_ENDPOINT = 'http://localhost:3000/'
const AUTH_TOKEN_KEY = 'authToken'

export const _login = async (email, password) => {
    try {
        let res = await axios({
            url: `/api/v1/users/login`,
            method: 'POST',
            data: {
                email: email,
                password: password
            }
        });

        setAuthToken(res.data.token);
        emit.$emit('refreshUsers');
        router.push('/');
    } catch (err) {
        // console.log(err.response.data.message);
        emit.$emit('errorDetected', err.response);
    }
}
export const _register = async (name, email, password, passwordConfirm) => {
    try {
        let res = await axios({
            url: `/api/v1/users/signup`,
            method: 'POST',
            data: {
                name: name,
                email: email,
                password: password,
                passwordConfirm: passwordConfirm
            }
        });

        setAuthToken(res.data.token);
        emit.$emit('refreshUsers');
        router.push('/');
    } catch (err) {
        // console.log(err.response);
        emit.$emit('errorDetected', err.response);
    }
}
export const _logout = async () => {
    try {
        await axios({
            url: `/api/v1/users/logout`,
            method: 'GET'
        });

        clearAuthToken()
        router.push('/login');
    } catch (err) {
        console.log(err.response.data);
    }
}

export const _getUser = async () => {
    if (isLoggedIn()) {
        // return new Promise(async (resolve, reject) => {});
        try {
            let res = await axios({
                url: `/api/v1/users/me`,
                method: 'GET',
                headers: { 'Authorization' : 'Bearer '+ getAuthToken()},
                withCredentials: true
            });
            return res.data.data.data;
        } catch (err) {
            console.log(err.response.data.message);
            return null;
        }
    }
}

export const _getUserId = async () => {
    if (isLoggedIn()) {
        // return new Promise(async (resolve, reject) => {});
        try {
            let res = await axios({
                url: `/api/v1/users/me`,
                method: 'GET',
                headers: { 'Authorization' : 'Bearer '+ getAuthToken()},
                withCredentials: true
            });
            return res.data.data.data._id;
        } catch (err) {
            console.log(err.response.data.message);
            return null;
        }
    }
}

export const setAuthToken = (token) => {
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;

    let expires= new Date(Date.now() + 90 * 24 * 60 * 60 * 1000);
    document.cookie = `xsrf-token=${token}; expires=${expires.toUTCString()}; path=/`;
}
export const getAuthToken = () => {
    return getCookie('xsrf-token');
}
export const clearAuthToken = () => {
    axios.defaults.headers.common['Authorization'] = '';

    let expires= new Date(Date.now() + (10 * 1000));
    document.cookie = `xsrf-token=; expires=${expires.toUTCString()}; path=/`;
}

export const isLoggedIn = () => {
    let authToken = getAuthToken()
    return !!authToken && !isTokenExpired(authToken)
}

function getTokenExpirationDate(encodedToken) {
    let token = decode(encodedToken)
    if (!token.exp) {
        return null
    }
  
    let date = new Date(0)
    date.setUTCSeconds(token.exp)
  
    return date
} 
function isTokenExpired(token) {
    let expirationDate = getTokenExpirationDate(token)
    return expirationDate < new Date()
}


function getCookie(name) {
    // Split cookie string and get all individual name=value pairs in an array
    var cookieArr = document.cookie.split(";");
    
    // Loop through the array elements
    for(var i = 0; i < cookieArr.length; i++) {
        var cookiePair = cookieArr[i].split("=");
        
        /* Removing whitespace at the beginning of the cookie name
        and compare it with the given string */
        if(name == cookiePair[0].trim()) {
            // Decode the cookie value and return
            return decodeURIComponent(cookiePair[1]);
        }
    }
    
    // Return null if not found
    return null;
}